export default function PoetryPage({onBack}) {
    const {settings, setSettings} = useContext(SettingsContext)

    const [index, setIndex] = useState(Settings.getData('poetry') || 0)
    return <BasePage
        naviTitle={''}
        onBack={onBack}
        naviRight={undefined}
    >
        <div
            style={{
                flex: 1, width: '100%', minHeight: 0,
                backgroundColor: settings.color[1],
                overflowY: 'auto',
            }}
        >
            <div
                style={{
                    flex: 1, minHeight: 0,
                    backgroundColor: settings.color[1],
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center', paddingRight: 20,
                    paddingLeft: 20,
                }}
            >
                {PoetryPool[index].split(/s|[\r\n]/g).map((t, i) => {
                    let fontSize = i == 1 ? 6 : 4
                    return <span
                        key={i.toString()}
                        style={{
                            fontSize: Settings.getFont(fontSize)[0],
                            lineHeight: Settings.getFont(fontSize)[1],
                            fontWeight: i == 1 ? 'bold' : undefined,
                            color: i == 1 ? settings.color[9] : settings.color[8],
                            marginTop: fontSize * 3,
                        }}
                    >{t}</span>
                })}

            </div>
        </div>
        <div
            style={{
                position: 'absolute', top: 60, right: 0,
                display: 'flex',
                flexDirection: 'row',
            }}
        >
            <Button
                color={'primary'}
                onClick={() => {
                    let i = index - 1
                    i = Math.max(0, i)
                    setIndex(i)
                    Settings.saveData('poetry', i)
                }}
            >
                   <span style={{
                       fontSize: Settings.getFont(4)[0],
                       lineHeight: Settings.getFont(4)[1],
                       whiteSpace: 'pre-line',
                   }}>{'\n'}{'\n'}</span>
            </Button>
            <Button
                style={{
                    marginLeft: 10,
                }}
                color={'primary'}
                onClick={() => {
                    let i = index + 1
                    i = Math.min(PoetryPool.length - 1, i)
                    setIndex(i)
                    Settings.saveData('poetry', i)
                }}
            >
                   <span style={{
                       fontSize: Settings.getFont(4)[0],
                       lineHeight: Settings.getFont(4)[1],
                       whiteSpace: 'pre-line',
                   }}>{'\n'}{'\n'}</span>
            </Button>
        </div>
    </BasePage>
}
